import React, { Component } from 'react';
import { connect } from 'react-redux';

import { GoogleLogin } from 'react-google-login'

import { updateUserAuthData } from '../../../store/actions'; 

class Google extends Component {

    constructor(props){
        super(props);
        this.state = {
            loggedIn : false
        }

        this.onLoginSuccessFull = this.onLoginSuccessFull.bind(this);
        this.onLoginError = this.onLoginError.bind(this);
        
    }

    componentWillMount(){
        // console.log(isSignedIn);
    }

    onLoginSuccessFull(response){
        console.log(response);
        let user = {};
        user["status"] = true;
        user["name"] = response.w3.ig;
        user["id"] = response.w3.U3;
        user["fName"] = response.w3.ofa;
        user["lName"] = response.w3.wea;
        user["accessToken"] = response.Zi.accessToken;
        user["tokenId"] = response.Zi.tokenId;
        this.props.updatingUserAuthData(user);
    }

    onLoginError(response){
        let user = {};
        user["error"] = true;
        user["errorData"] = response;
        this.props.updatingUserAuthData(user);
    }

    render() {
        
        return (
            <div className="auth_google">
                <GoogleLogin
                    onSuccess={res => { 
                        this.onLoginSuccessFull(res)
                    }}
                    onFailure={this.onLoginError}
                    clientId={this.props.config.clientId}
                    isSignedIn
                >
                    <span>Sign-in With Google</span>
                </GoogleLogin>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    config: state.config
});
  
const mapDispatchToProps = dispatch =>({
    updatingUserAuthData(status){
        dispatch(updateUserAuthData(status))
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(Google);